<template>
    <section v-if="copiedToy" class="flex column align-center space-around toy-edit">
        <router-link to="/toy" class="close-btn"><button>X</button></router-link>
        <h1>{{title}}</h1>
        <form @submit.prevent="saveToy" class="flex column align-center justify-center">
            <table>
                <tr>
                    <td><label for="toyName">Name:</label></td>
                    <td><input id="toyName" type="text" placeholder="Toy name" v-model="copiedToy.name"/></td>
                </tr>
                <tr>
                    <td><label for="toyCategory">Type:</label></td>
                    <td><input id="toyCategory" type="text" placeholder="type" list="toyCategories" v-model="copiedToy.type"/></td>
                    <datalist id="toyCategories">
                        <option v-for="category in categories" :value="category" :label="category"/>
                    </datalist>
                </tr>
                <tr>
                    <td><label for="toyPrice">Price:</label></td>
                    <td><input id="toyPrice" type="number" v-model.number="copiedToy.price" min="0"/></td>
                </tr>
                <tr>
                    <td><label for="toyImg">Image:</label></td>
                    <td><input id="toyImg" type="file" @change="setImg"/></td>
                </tr>
            </table>
            <button>Save</button>
        </form>
    </section>
</template>

<script>
    export default {
        name: 'toy-edit',
        data() {
            return {
                copiedToy: null
            }
        },
        computed: {
            categories() {
                return this.$store.getters.toyCategories;
            },
            title() {
                return (this.copiedToy._id)? 'Edit toy' : 'Add new toy';
            }
        },
        methods: {
            getToy() {
                var id = this.$route.params._id;
                this.$store.dispatch({type: 'getToy', toyId: id})
                    .then(toy => {
                        console.log(toy)
                        this.copiedToy = JSON.parse(JSON.stringify(toy))})
            },
            saveToy() {
                this.$store.dispatch({type: 'saveToy', toy: {...this.copiedToy}});
                this.$router.push('/toy')
            },
            setImg(ev) {
                this.$store.dispatch({type: 'getToyImg', ev})
            }
        },
        created() {
            this.getToy()
        },
        watch: {
            '$route.params.id'(id) {
                this.getToy();
            }
        }
    }
</script>