<template>
    <section class="flex column align-center toy-preview">
        <div class="toy-infp-container flex align-center space-around wrap">
            <div class="flex column align-center">
                <h3 class="text-center toy-title">{{toy.name}}</h3>
                <h4>Type: {{toy.type}}</h4>
                <h4>Price: {{toy.price}}</h4>
                <h4>{{isInStockMsg}}</h4>
            </div>
            <img :src="toy.imgUrl || `https://api.adorable.io/avatars/285/${toy.name}.png`" alt="No image available">
        </div>
        <div class="buttons-container flex align-center space-around wrap">
            <router-link :to="'/toy/edit/'+toy._id"><button>Edit</button></router-link>
            <button @click="removeToy">Remove</button>
            <router-link :to="'/toy/'+toy._id"><button>More details</button></router-link>
        </div>
    </section>
</template>

<script>

export default {
    name: 'toy-preview',
    props: ['toy'],
    computed: {
        isInStockMsg() {
            return (this.toy.isInStock)? 'In stock' : 'Not in stock';
        }
    },
    methods: {
        removeToy() {
            this.$store.dispatch({type: 'getConfirm', msg: 'Are you sure you want to remove this toy?'})
                .then(() => {
                    this.$store.dispatch({type: 'removeToy', toyId: this.toy._id});
                })
        }
    }
}

</script>