<template>
    <section class="toy-app">
        <h1 class="text-center">TOYS</h1>
        <div class="flex align-center space-around wrap width-all">
            <toyFilter/>
            <router-link to="/toy/edit"><button>Add toy</button></router-link>
        </div>
        <router-view/>
        <toy-list :toys="toys"/>
    </section>
</template>


<script>

import toyList from '../components/toy-list.cmp.vue';
import toyFilter from '../components/toy-filter.cmp.vue';

// import confirmAlert from '../components/confirm-popup.cmp.vue';


export default {
    name: 'toy-app',
    computed: {
        toys() {
            return this.$store.getters.toysToShow;
        }
    },
    components: {
        toyList,
        toyFilter
    },
    created() {
        this.$store.dispatch('getToys');
    }
}
</script>