<template>
    <form @submit.prevent="searchToys" class="flex align-center justify-center wrap toy-filter">
        <input @change="searchToys" type="text" v-model="filterBy.searchStr" placeholder="Search"/>
        <select @change="searchToys" v-model="filterBy.category">
            <option value="All" label="All"/>
            <option v-for="category in categories" :key="category" :value="category" :label="category"/>
        </select>
        <input @change="searchToys" type="number" v-model.number="filterBy.maxPrice" min="0"/>
    </form>
</template>

<script>
export default {
    name: 'toys-filter',
    data() {
        return {
            filterBy: {},
        }
    },
    computed: {
        categories() {
            return this.$store.getters.toyCategories;
        }
    },
    methods: {
        searchToys() {
            this.$store.commit({type: 'setFilter', filterBy: this.filterBy})
        }
    },
    created() {
        this.filterBy = {...this.$store.getters.toysFilterBy};
    }
}
</script>