<template>
    <section class="toy-details flex column align-center space-around">
        <!-- <router-link to="/toy">back</router-link> -->
        <h1>Toy details:</h1>
        <pre>{{currToy}}</pre>
    </section>
</template>

<script>
export default {
    name: 'toy-details',
    computed: {
        currToy() {
            return this.$store.getters.currToy;
        },
    },
    methods: {
        getToy() {
            var _id = this.$route.params._id;
            this.$store.dispatch({type: 'getToy', toyId: _id})
        },
    },
    created() {
        this.getToy()
    },
    watch: {
        '$route.params._id'(_id) {
            this.getToy();
        }
    }
}
</script>