'use strict';

// import utils from './util.service.js';

// import {uploadImg} from './cloud-file.service.js';

import httpService from './http.service.js';


const BASE_API = (process.env.NODE_ENV !== 'development') 
        ?'/api/toy/'    
        :'//localhost:3030/api/toy/';

export default {
    query,
    get,
    save,
    remove
}

function remove(_id) {
    return httpService.remove(BASE_API+_id);
}

function get(_id) {
    if (!_id) return Promise.resolve(_getNewToy());
    return httpService.get(BASE_API+_id);
}

function save(toy) {
    // if (!toy.imgUrl) toy.imgUrl = `https://api.adorable.io/avatars/285/${toy.name}.png`;
    if (toy._id) {
        return httpService.put(BASE_API+toy._id, toy)
            .then(() => toy);
    } else {
        return httpService.post(BASE_API, toy);
    }
}

function query() {
    return httpService.get(BASE_API);
}

function _getNewToy() {
    return {
        name: '',
        price: 0,
        type: '',
        createdAt: Date.now(),
        isInStock: true
    }
}

