'use strict';

import toyService from '../services/toy.service.js';
import {uploadImg} from '../services/cloud-file.service.js';

export default {
    state: {
        toys: [],
        currToy: null,
        filterBy: {
            searchStr: '',
            maxPrice: 0,
            category: 'All'
        },
        imgUrl: null
    },
    getters: {
        toysToShow(state) {
            var filter = state.filterBy;
            var toysToShow = [...state.toys];

            toysToShow = toysToShow.filter(toy => toy.name.toLowerCase().includes(filter.searchStr.toLowerCase()));
            toysToShow = toysToShow.filter(toy => filter.category === 'All' || toy.type === filter.category);
            toysToShow = toysToShow.filter(toy => !filter.maxPrice || toy.price <= filter.maxPrice);

            return toysToShow;
        },
        toysFilterBy(state) {
            return state.filterBy;
        },
        toyCategories(state) {
            return state.toys.reduce((acc, toy) => {
                if (toy.type && !acc.includes(toy.type)) acc.push(toy.type);
                return acc;
            }, []) || [];
        },
        currToy(state) {
            return state.currToy;
        }
    },
    mutations: {
        setToys(state, {toys}) {
            state.toys = toys;
        },
        removeToy(state, {id}) {
            var idx = state.toys.findIndex(toy => toy._id === id);
            if (idx !== -1) {
                state.toys.splice(idx, 1);
            }
        },
        saveToy(state, {toy}) {
            var idx = state.toys.findIndex(currToy => currToy._id === toy._id);
            if (idx !== -1) {
                state.toys.splice(idx, 1, toy);
            } else {
                state.toys.unshift(toy);
            }
        },
        setCurrToy(state, {toy}) {
            state.currToy = toy;
        },
        setFilter(state, {filterBy}) {
            state.filterBy = filterBy;
        }
    },
    actions: {
        getToys(context) {
            toyService.query()
                .then(toys => context.commit({type: 'setToys', toys}))
                .catch(err => console.log(err));
        },
        getToy(context, {toyId}) {
            return toyService.get(toyId)
                .then(toy => {
                    context.commit({type: 'setCurrToy', toy});
                    return toy;
                });
        },
        removeToy(context, {toyId}) {
            toyService.remove(toyId)
                .then(id => context.commit({type: 'removeToy', id}))
        },
        doSaveToy(context, {toy}) {
            return toyService.save(toy)
                .then(toy => {
                    context.commit({type: 'saveToy', toy});
                    return toy;
                })
        },
        saveToy(context, {toy}) {
            if (context.state.imgUrl) {
                toy.imgUrl = context.state.imgUrl;
                console.log(toy.imgUrl)
                context.state.imgUrl = null;
                return context.dispatch({type: 'doSaveToy', toy})
                // return toyService.save(toy)
                //     .then(toy => {
                //         context.commit({type: 'saveToy', toy});
                //         return toy;
                //     })
            }
            return context.dispatch({type: 'doSaveToy', toy})
            // return toyService.save(toy)
            //     .then(toy => {
            //         context.commit({type: 'saveToy', toy});
            //         return toy;
            //     })
        },
        getToyImg(context, {ev}) {
            uploadImg(ev)
                .then(data => context.state.imgUrl = data.secure_url)
                .catch(err => console.log(err));
        }
    }
}